@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/Roboto-Condensed.woff2") format("woff2"), url("../fonts/Roboto-Condensed.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Light.woff2") format("woff2"), url("../fonts/RobotoCondensed-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/AlumniSans-SemiBold.woff2") format("woff2"), url("../fonts/AlumniSans-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/AlumniSans-Bold.woff2") format("woff2"), url("../fonts/AlumniSans-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/AlumniSans-ExtraBold.woff2") format("woff2"), url("../fonts/AlumniSans-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Alumni Sans", "Arial", sans-serif;
  color: #102654;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f8f9fc;
}

a {
  color: #102654;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #102654;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 30px 103px;
}
@media (max-width: 1199px) {
  .container {
    padding: 30px 45px;
  }
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  width: 100%;
  font-weight: 600;
  font-size: 14px;
  line-height: 150%;
  color: #102654;
  padding: 10px 14px;
  cursor: pointer;
  background-color: #ffffff;
  transition: all 0.3s ease-in-out;
  user-select: none;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.btn:focus-visible,
.btn:focus,
.btn:hover {
  color: #102654;
  background-color: #ffffff;
}

.btn:active {
  opacity: 0.4;
}

.header {
  min-height: 658px;
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  /*
  @include vp-1199 {

  }
  */
  /*
  @include vp-767 {

  }
  */
}

.header__top {
  display: flex;
  justify-content: space-between;
}

.header__logo {
  margin-top: 10px;
}
@media (max-width: 1199px) {
  .header__logo {
    width: 82px;
  }
}

.header__phone {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  color: #ffffff;
  margin-top: 0;
}
@media (max-width: 1199px) {
  .header__phone {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.02em;
    margin-top: 5px;
  }
}
.header__phone:hover, .header__phone:focus-visible {
  transition: all 0.3s ease-in-out;
  color: #102654;
  opacity: 0.8;
}
.header__phone:active {
  opacity: 0.5;
}

.header__intro {
  display: grid;
  grid-template-columns: 45% 1fr;
  gap: 30px;
  padding-right: 17.2%;
  margin-top: -12px;
}
@media (max-width: 1199px) {
  .header__intro {
    grid-template-columns: 48% 1fr;
    padding-right: 0;
    padding-top: 30px;
  }
}

.header__content span {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  letter-spacing: 0.03em;
  color: #ffffff;
  display: flex;
  margin-top: 112px;
  margin-left: 263px;
}
@media (max-width: 1199px) {
  .header__content span {
    font-size: 34px;
    line-height: 41px;
    margin-top: 50px;
  }
}

.header__info {
  display: flex;
}

.header h1 {
  text-transform: uppercase;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  text-align: right;
  letter-spacing: 0.05em;
  color: #ffffff;
  width: 250px;
  margin: 13px 0 50px;
  position: relative;
  left: 7%;
}
@media (max-width: 1199px) {
  .header h1 {
    text-transform: none;
    width: 191px;
    font-size: 72px;
    line-height: 86px;
    margin-top: 7px;
    margin-bottom: 37px;
  }
}

.header__list {
  margin: 135px 0 0 0;
  list-style: none;
  padding: 0;
  position: relative;
  left: 4%;
  width: 115px;
}
@media (max-width: 1199px) {
  .header__list {
    margin: 95px 20px 0 0;
  }
}

.header__item {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  text-align: right;
  color: #ffffff;
}

.header__button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: transparent;
  border: 4px solid #ffffff;
  min-height: 60px;
  width: 54.5%;
  margin-left: 163px;
}
@media (max-width: 1199px) {
  .header__button {
    width: 53%;
    margin-left: 139px;
  }
}

